html body {
	 padding: 0;
	 margin: 0;
	 font-family: "Poppins", -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
}
 .hero {
	 margin-top: 5%;
	 overflow: hidden;
	 visibility: hidden;
	 height: 100vh;
}
 .hero  {
	 width: 1440px;
	 min-width: 1440px;
	 margin: 0 auto;
}
 .hero       .hero-inner {
	 display: flex;
	 align-items: center;
	 justify-content: space-between;
	 margin: 0 24px;
	 height: 100vh;
}
 .hero       .hero-inner .hero-content {
	 display: flex;
     
	 width: 50%;
}
 .hero       .hero-inner .hero-content .hero-content-inner {
	 width: 400px;
	 color: #323232;
	 margin: 0 auto;
}
 .hero       .hero-inner .hero-content .hero-content-inner h1 {
	 font-weight: 500;
	 font-size: 32px;
	 margin-bottom: 24px;
}
 .hero       .hero-inner .hero-content .hero-content-inner h1 .hero-content-line {
	 margin: 0;
	 height: 44px;
	 overflow: hidden;
}
 .hero       .hero-inner .hero-content .hero-content-inner p {
	 font-size: 14px;
	 line-height: 24px;
	 font-weight: 300;
	 padding-right: 48px;
	 margin-bottom: 56px;
}
 .hero       .hero-inner .hero-content .hero-content-inner .btn-row {
	 position: relative;
}
 .hero       .hero-inner .hero-content .hero-content-inner .btn-row:before {
	 position: absolute;
	 content: "";
	 width: 56px;
	 height: 1px;
	 background: #dfdfdf;
	 top: 50%;
	 left: -68px;
}
 .hero       .hero-inner .hero-content .hero-content-inner .btn-row button {
	 margin: 1px;
	 background: none;
	 border: none;
	 font-size: 12px;
	 padding: 20px;
	 cursor: pointer;
	 text-transform: uppercase;
	 font-weight: 700;
	 letter-spacing: 0.7px;
	 display: flex;
	 align-items: center;
	 position: relative;
}
 .hero       .hero-inner .hero-content .hero-content-inner .btn-row button:focus {
	 outline: none;
}
 .hero       .hero-inner .hero-content .hero-content-inner .btn-row button .arrow-icon {
	 margin: 2%;
	 padding: 1%;
	 position: absolute;
	 display: flex;
	 align-items: center;
	 justify-content: center;
	 border-radius: 5px;
}
 .hero       .hero-inner .hero-content .hero-content-inner .btn-row button .arrow-icon .arrow-icon:hover {
	 background-color: #007fff;
	 border-radius: 5px;
}
 .hero       .hero-inner .hero-content .hero-content-inner .btn-row button .arrow-icon img {
	margin: 63px;
    padding: 1%;
}
 .hero       .hero-inner .hero-images {
	 width: 50%;
	 height: 100vh;
	 position: relative;
}
 .hero       .hero-inner .hero-images .hero-images-inner .hero-image {
	 position: absolute;
	 overflow: hidden;
}
 .hero       .hero-inner .hero-images .hero-images-inner .hero-image.girl {
	 top: 0;
	 right: 0px;
	 width: 70%;
	 height: 80%;
}
 .hero       .hero-inner .hero-images .hero-images-inner .hero-image.boy {
	 bottom: 24px;
	 left: 0px;
	 width: 52%;
	 height: 80%;
}
 .hero       .hero-inner .hero-images .hero-images-inner .hero-image img {
	 position: absolute;
	 top: 0;
	 left: 0;
	 right: 0;
	 bottom: 0;
	 width: 100%;
}
 
